.App {
  height: auto;
  min-height: 500px;
  width: 750px;
  display: flex;
  justify-content: center;
  align-items:center;
  position:relative;
  border-radius: 10px;
  border: lightgrey 1px solid;
  background-color:snow;
  overflow:hidden;
}

.svg-blue {
  position:absolute;
  bottom:0;
  left:0;
  z-index:0;
}

.svg-yellow {
  position:absolute;
  top:0;
  right:0;
  z-index:0;
}